.card-container{
    width: 15rem;
    border-radius: 1rem;
    box-shadow: 0px 10px 8px #999;
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
    background-color: white;
    height: fit-content;  
    /* sets the height whenever there are some elements are missing */

}

.card-img{
    width: 100%;
    border-radius: 1rem 1rem 0 0;
}

.card-title,
.card-description{
    margin: 0.5rem 5%;
}

.card-btn{
    text-align: center;
    width: 90%;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    background-color: #2363eb;
    text-decoration: none;
    color: white;
    margin: 0.5rem auto;
}
